<template>
  <li class="category__item">
    {{ category.name }}
    <span class="category__buttons">
      <button v-on:click="editCategory" class="button__icon">
        <pencil-outline-icon fillColor="#2c3e50" class="icon-button" />
      </button>
      <button v-on:click="deleteCategory" class="button__icon">
        <delete-icon fillColor="#2c3e50" class="icon-button" />
      </button>
    </span>
  </li>
</template>

<script>
import EditCategoryModal from "@/components/modals/EditCategoryModal.vue";

export default {
  data: function() {
    return {};
  },
  computed: {},
  props: ["category", "masterData"],
  name: "Category",
  components: {},
  filters: {},
  methods: {
    editCategory: function() {
      this.$modal.show(
        EditCategoryModal,
        {
          title: "Wijzig categorie",
          id: this.category.id,
          name: this.category.name,
          cat_key: this.category.key
        },
        {
          scrollable: true,
          adaptive: true,
          height: "auto"
        }
      );
    },
    deleteCategory: function() {
      this.$store.dispatch("deleteCategory", { key: this.category.key });
      this.$store.dispatch("setCategories");
    }
  }
};
</script>

