<template>
  <li class="customer__item">
    <img v-if="customer.image" class="customer__image" :src="customer.image" />
    <span v-else>
      <image-frame-icon fillColor="#2c3e50" />
    </span>
    {{ customer.name }}
    <span class="customer__buttons">
      <button v-on:click="editCustomer" class="button__icon">
        <pencil-outline-icon fillColor="#2c3e50" class="icon-button" />
      </button>
      <button v-on:click="deleteCustomer" class="button__icon">
        <delete-icon fillColor="#2c3e50" class="icon-button" />
      </button>
    </span>
  </li>
</template>

<script>
import EditcustomerModal from "@/components/modals/EditCustomerModal.vue";

export default {
  data: function() {
    return {};
  },
  computed: {},
  props: ["customer", "masterData"],
  name: "customer",
  components: {},
  filters: {},
  methods: {
    editCustomer: function() {
      this.$modal.show(
        EditcustomerModal,
        {
          title: "Wijzig bedrijf",
          id: this.customer.id,
          name: this.customer.name,
          cat_key: this.customer.key,
          image: this.customer.image
        },
        {
          scrollable: true,
          adaptive: true,
          height: "auto"
        }
      );
    },
    deleteCustomer: function() {
      this.$store.dispatch("deleteCustomer", { key: this.customer.key });
      this.$store.dispatch("setCustomers");
    }
  }
};
</script>

