<template>
  <div>
    <h1>({{ categories.length }}) Categorien</h1>
    <button v-if="user.loggedIn" @click="newCategory" class="button__text">
      Voeg categorie toe
    </button>
    <tc-categories
      v-if="user.loggedIn"
      :categories="categories"
      :masterData="masterData"
    />
    <div v-else>
      Je moet inloggen om dit te mogen zien
      <router-link to="/login">login</router-link>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import Categories from "@/components/categories/Categories.vue";
import NewCategoryModal from "@/components/modals/NewCategoryModal.vue";
import { mapGetters } from "vuex";

export default {
  beforeCreate: function() {
    this.$store.dispatch("setCategories");
    this.$store.dispatch("setMasterData");
  },
  data: function() {
    return {};
  },
  computed: {
    masterData() {
      return this.$store.getters.getMasterData;
    },
    categories() {
      return this.$store.getters.getCategories;
    },
    ...mapGetters({
      user: "user"
    })
  },
  name: "CategoriesPage",
  components: {
    "tc-categories": Categories
  },
  methods: {
    newCategory: function() {
      this.$modal.show(
        NewCategoryModal,
        {
          title: "Nieuwe categorie",
          id: -1,
          name: ""
        },
        {
          scrollable: true,
          adaptive: true,
          height: "auto"
        }
      );
    }
  }
};
</script>
