<template>
  <div>
    <h1>({{ contacts.length }}) Contactpersonen</h1>
    <button v-if="user.loggedIn" @click="newContact" class="button__text">
      Voeg contactpersoon toe
    </button>
    <tc-contacts
      v-if="user.loggedIn"
      :contacts="contacts"
      :masterData="masterData"
    />
    <div v-else>
      Je moet inloggen om dit te mogen zien
      <router-link to="/login">login</router-link>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import Contacts from "@/components/contacts/Contacts.vue";
import NewContactModal from "@/components/modals/NewContactModal.vue";
import { mapGetters } from "vuex";

export default {
  beforeCreate: function() {
    this.$store.dispatch("setContacts");
    this.$store.dispatch("setMasterData");
  },
  data: function() {
    return {};
  },
  computed: {
    masterData() {
      return this.$store.getters.getMasterData;
    },
    contacts() {
      return this.$store.getters.getContacts;
    },
    ...mapGetters({
      user: "user"
    })
  },
  name: "ContactsPage",
  components: {
    "tc-contacts": Contacts
  },
  methods: {
    newContact: function() {
      this.$modal.show(
        NewContactModal,
        {
          title: "Nieuw contactpersoon",
          id: -1,
          name: "",
          mail: "",
          slackName: ""
        },
        {
          scrollable: true,
          adaptive: true,
          height: "auto"
        }
      );
    }
  }
};
</script>
