<template>
  <div>
    <h1>({{ customers.length }}) Bedrijven</h1>
    <button v-if="user.loggedIn" @click="newCustomer" class="button__text">
      Voeg bedrijf toe
    </button>
    <tc-customers
      v-if="user.loggedIn"
      :customers="customers"
      :masterData="masterData"
    />
    <div v-else>
      Je moet inloggen om dit te mogen zien
      <router-link to="/login">login</router-link>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import Customers from "@/components/customers/Customers.vue";
import NewCustomerModal from "@/components/modals/NewCustomerModal.vue";
import { mapGetters } from "vuex";

export default {
  beforeCreate: function() {
    this.$store.dispatch("setCustomers");
    this.$store.dispatch("setMasterData");
  },
  data: function() {
    return {};
  },
  computed: {
    masterData() {
      return this.$store.getters.getMasterData;
    },
    customers() {
      return this.$store.getters.getCustomers;
    },
    ...mapGetters({
      user: "user"
    })
  },
  name: "CustomersPage",
  components: {
    "tc-customers": Customers
  },
  methods: {
    newCustomer: function() {
      this.$modal.show(
        NewCustomerModal,
        {
          title: "Nieuwe bedrijf",
          id: -1,
          name: "",
          image: ""
        },
        {
          scrollable: true,
          adaptive: true,
          height: "auto"
        }
      );
    }
  }
};
</script>
