<template>
  <li class="contact__item">
    <span class="contact__name">{{ contact.name }}</span>
    <span class="contact__slackName">
      <slack-icon fillColor="#2c3e50" />
      {{ contact.slackName }}</span
    >
    <span class="contact__mail">
      <email-outline-icon fillColor="#2c3e50" />
      {{ contact.mail }}</span
    >
    <span class="contact__buttons">
      <button v-on:click="editContact" class="button__icon">
        <pencil-outline-icon fillColor="#2c3e50" class="icon-button" />
      </button>
      <button v-on:click="deleteContact" class="button__icon">
        <delete-icon fillColor="#2c3e50" class="icon-button" />
      </button>
    </span>
  </li>
</template>

<script>
import EditcontactModal from "@/components/modals/EditContactModal.vue";

export default {
  data: function() {
    return {};
  },
  computed: {},
  props: ["contact", "masterData"],
  name: "contact",
  components: {},
  filters: {},
  methods: {
    editContact: function() {
      this.$modal.show(
        EditcontactModal,
        {
          title: "Wijzig contactpersoon",
          id: this.contact.id,
          name: this.contact.name,
          slackName: this.contact.slackName,
          mail: this.contact.mail,
          cat_key: this.contact.key
        },
        {
          scrollable: true,
          adaptive: true,
          height: "auto"
        }
      );
    },
    deleteContact: function() {
      this.$store.dispatch("deleteContact", { key: this.contact.key });
      this.$store.dispatch("setContacts");
    }
  }
};
</script>

