<template>
  <div class="">Logged out</div>
</template>

<script>
import firebase from "firebase/firebase";

export default {
  data() {
    return {};
  },
  created: function() {
    // `this` points to the vm instance
    this.signOut();
  },
  methods: {
    signOut() {
      firebase
        .auth()
        .signOut()
        .then(() => {
          this.$router.replace({
            name: "Login"
          });
        });
    }
  }
};
</script>
