<template>
  <div class="newContactModal">
    <button @click="closeModal" class="close-modal">
      <close-icon fillColor="#2c3e50" class="icon-button" />
    </button>
    <h1>{{ title }}</h1>
    <vue-form :state="formstate" @submit.prevent="onSubmit">
      <span>Contactnaam *</span>
      <input
        v-model="model.name"
        class="input__styling"
        required
        name="name"
        v-bind:class="{ error: error.name }"
      />

      <span>Naam in slack</span>
      <input
        v-model="model.slackName"
        class="input__styling"
        name="slackName"
      />

      <span>Email</span>
      <input v-model="model.mail" class="input__styling" name="mail" />

      <div class="messages" v-if="this.error.messages.length > 0">
        <ul class="errorContainer">
          <li v-for="error in this.error.messages" :key="error">
            {{ error }}
          </li>
        </ul>
      </div>
      <button class="buttons__button buttons__button--icon" type="submit">
        Opslaan
      </button>
    </vue-form>
  </div>
</template>

<script>
export default {
  props: ["id", "name", "title", "slackName", "mail"],
  name: "add-contact",
  data: function() {
    return {
      formstate: {},
      model: {
        name: "",
        mail: "",
        slackName: ""
      },
      error: {
        name: false,
        messages: []
      }
    };
  },
  methods: {
    onSubmit: function() {
      const checkFields = [
        { name: "name", message: "De naam is niet correct ingevuld" }
      ];
      const _this = this;
      this.error.messages = [];
      checkFields.forEach(field => {
        if (!_this.model[field.name]) {
          _this.error.messages.push(field.message);
          _this.error[field.name] = true;
        } else {
          _this.error[field.name] = false;
          if (_this.model[field.name].length === 0) {
            _this.error[field.name] = true;
            _this.error.messages.push(field.message);
          }
        }
      });
      if (!this.model.name) {
        console.log("Fix eerst je form a.u.b");
        return;
      }
      this.$store.dispatch("addContact", {
        name: this.model.name,
        slackName: this.model.slackName,
        mail: this.model.mail
      });
      this.$store.dispatch("setContacts");
      this.$emit("close");
    },
    closeModal: function() {
      this.$emit("close");
    }
  }
};
</script>

<style lang="scss">
.newContactModal {
  padding: 20px;
}
</style>
